.hovercomp {
    position: relative;
    display: inline-block;
    margin: 100px 0 0 150px;
  }
  
  .btntext {
    padding: 10px 20px;
    font-size: 16px;
    color: #333;
    background-color: #f9f9f9;
    border: 2px dashed #b5b5e4;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .btntext:hover + .hovercontent,
  .hovercontent:hover {
    visibility: visible;
    opacity: 1;
  }
  
  .hovercontent {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    margin-bottom: 30px;
    padding: 8px 12px;
    background-color: #000;
    color: #fff;
    border-radius: 5px;
    font-size: 14px;
    white-space: nowrap;
    text-align: center;
    transition: opacity 0.2s ease;
  }
  
  .tooltipcontent::after {
    content: "";
    position: absolute;
    top: 100%; /* Arrow at the bottom of the tooltip */
    left: 50%;
    transform: translateX(-50%);
    border-width: 5px;
    border-style: solid;
    border-color: #000 transparent transparent transparent;
  }