.indicator-container {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    border-radius: 10px;
    width: 150px;
    box-shadow: 0px 4px 6px rgba(10, 9, 9, 0.1);
    transition: background-color 0.3s ease;
  }
  
  .indicator-container.day {
    background-color: #fff5e6;
    color: #050404;
  }
  
  .indicator-container.night {
    background-color: #2c2c54;
    color: #d9d9d9;
  }
  
  .icon {
    margin-right: 10px;
  }
  
  .text {
    font-size: 18px;
    font-weight: bold;
  }
  