.search-container {
    display: flex;
    align-items: center;
    border: 1px solid #ccc;
   
    padding: 5px 10px;
    width: 300px;
    background-color: #fff;
  }
  
  .icon-container {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 5px;
    color: #888; 
  }
  
  .search-input {
    flex: 1;
    border: none;
    outline: none;
    padding: 5px;
    font-size: 14px;
    color: #333;
  }
  
  .search-input::placeholder {
    color: #aaa;
    font-style: italic;
  }